/* button red  */
#but-red{text-decoration:none; text-align:center; 
  padding:11px 46px; 
  border:solid 1px #000000; 
  -webkit-border-radius:4px;
  -moz-border-radius:4px; 
  border-radius: 4px; 
  font:24px Arial, Helvetica, sans-serif; 
  font-weight:bold; 
  color:#ffffff; 
  background:#e90536; 
  -webkit-box-shadow:inset 0,0,0px 0,0,0px 1,1,1px #ffffff,#ffffff,#ffffff;  -moz-box-shadow:inset 0px 0px 1px #ffffff;  box-shadow:inset 0px 0px 1px #ffffff;  
   
    -webkit-transition: color 0.55s linear;
  -moz-transition:  color 0.55s linear;
  -o-transition:  color 0.55s linear;
  transition:  color 0.55s linear;}.button:hover{
  padding:11px 46px; 
  border:solid 1px #000000; 
  -webkit-border-radius:4px;
  -moz-border-radius:4px; 
  border-radius: 4px; 
  font:24px Arial, Helvetica, sans-serif; 
  font-weight:bold; 
  color:#ffffff; 
  background-color:#e90536; 
  background-image: -moz-linear-gradient(top, #e90536 0%, #db889a 100%); 
  background-image: -webkit-linear-gradient(top, #e90536 0%, #db889a 100%); 
  background-image: -o-linear-gradient(top, #e90536 0%, #db889a 100%); 
  background-image: -ms-linear-gradient(top, #e90536 0% ,#db889a 100%); 
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#db889a', endColorstr='#db889a',GradientType=0 ); 
  background-image: linear-gradient(top, #e90536 0% ,#db889a 100%);   
  -webkit-box-shadow:inset 0,0,0px 0,0,0px 1,1,1px #ffffff,#ffffff,#ffffff;  -moz-box-shadow:inset 0px 0px 1px #ffffff;  box-shadow:inset 0px 0px 1px #ffffff;  
  }

  #but-red:active{
  padding:11px 46px; 
  border:solid 1px #000000; 
  -webkit-border-radius:4px;
  -moz-border-radius:4px; 
  border-radius: 4px; 
  font:24px Arial, Helvetica, sans-serif; 
  font-weight:bold; 
  color:#ffffff; 
  background:#db889a; 
  }
  
  #but-red a {
    color: #bababa;
    text-decoration: none; 
  }
  
/* button white */

#but-white{text-decoration:none; text-align:center; 
    padding:11px 46px; 
    border:solid 1px #000000; 
    -webkit-border-radius:4px;
    -moz-border-radius:4px; 
    border-radius: 4px; 
    font:24px Arial, Helvetica, sans-serif; 
    font-weight:bold; 
    color:#000000; 
    background:#ffffff; 
    -webkit-box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff; 
    -moz-box-shadow: 0px 0px 2px #bababa,  inset 0px 0px 1px #ffffff;  
    box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff;  
     
  }.button:hover{
    padding:11px 46px; 
    border:solid 1px #000000; 
    -webkit-border-radius:4px;
    -moz-border-radius:4px; 
    border-radius: 4px; 
    font:24px Arial, Helvetica, sans-serif; 
    font-weight:bold; 
    color:#000000; 
    background-color:#ffffff; 
    background-image: -moz-linear-gradient(top, #ffffff 0%, #c4c4c4 100%); 
    background-image: -webkit-linear-gradient(top, #ffffff 0%, #c4c4c4 100%); 
    background-image: -o-linear-gradient(top, #ffffff 0%, #c4c4c4 100%); 
    background-image: -ms-linear-gradient(top, #ffffff 0% ,#c4c4c4 100%); 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c4c4c4', endColorstr='#c4c4c4',GradientType=0 ); 
    background-image: linear-gradient(top, #ffffff 0% ,#c4c4c4 100%);   
    -webkit-box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff; 
    -moz-box-shadow: 0px 0px 2px #bababa,  inset 0px 0px 1px #ffffff;  
    box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff;  
     
}
#but-white:active{
    padding:11px 46px; 
    border:solid 1px #000000; 
    -webkit-border-radius:4px;
    -moz-border-radius:4px; 
    border-radius: 4px; 
    font:24px Arial, Helvetica, sans-serif; 
    font-weight:bold; 
    color:#000000; 
    background:#c4c4c4; 
    -webkit-box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff; 
    -moz-box-shadow: 0px 0px 2px #bababa,  inset 0px 0px 1px #ffffff;  
    box-shadow:0px 0px 2px #bababa, inset 0px 0px 1px #ffffff;  
}

#but-white a {
    color: black;
    text-decoration: none; 
}

